  font-size: 18px;\n  font-weight: 600;\n  color: #ffffff;\n}\n.team-section .team-items .item .inner .info p {\n  font-size: 16px;\n  font-weight: 400;\n  color: #c5c5c5;\n  margin: 10px 0 0;\n}\n.team-section .team-items .item .inner .info .social-links {\n  padding-top: 15px;\n}\n.team-section .team-items .item .inner .info .social-links a {\n  display: inline-block;\n  height: 32px;\n  width: 32px;\n  background-color: #ffffff;\n  color: #009688;\n  border-radius: 50%;\n  margin: 0 2px;\n  text-align: center;\n  line-height: 32px;\n  font-size: 16px;\n  -webkit-transition: all 0.5s ease;\n  transition: all 0.5s ease;\n}\n.team-section .team-items .item .inner .info .social-links a:hover {\n  box-shadow: 0 0 10px #000;\n}\n/*responsive*/\n@media (max-width: 991px) {\n  .team-section .team-items .item {\n    flex-basis: calc(50% - 30px);\n    max-width: calc(50% - 30px);\n  }\n}\n@media (max-width: 767px) {\n  .team-section .team-items .item {\n    flex-basis: calc(100%);\n    max-width: calc(100%);\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"